import { useState, useEffect } from "react";
import Link from "next/link";
import Router from "next/router";
import { isAuth, getCookie } from "../../actions/auth";
import { create, getCategories, removeCategory, singleCategory } from "../../actions/category";

const Category = () => {
    const [values, setValues] = useState({
        name: "",
        error: false,
        success: false,
        categories: [],
        removed: false
    });

    const { name, error, success, categories, removed } = values;
    const token = getCookie("token");

    useEffect(() => {
        // loadCategories();
        categorieslistData();
    });

    const clickSubmit = e => {
        e.preventDefault();
        create({ name }, token).then(data => {
            if (data.error) {
                setValues({ ...values, error: data.error, success: false });
            } else {
                console.log(data);
                setValues({ ...values, error: false, success: true, name: "" });
            }
        });
        console.log("create category :", name);
    };

    const categorieslistData = () => {
        getCategories().then(data => {
            if (data.error) {
                console.log(data.error);
            } else {
                console.log(data);
                categoryListTable(data);
            }
        });
    };
    const categoryListTable = data => {
        let datalist = [...data];

        let viewdatalist = datalist.map(el => {
            return <div>{el.name}</div>;
        });

        return <React.Fragment>{viewdatalist}</React.Fragment>;
    };

    const handleChange = e => {
        setValues({
            ...values,
            name: e.target.value,
            error: false,
            success: false,
            removed: ""
        });
    };

    const newCategoryForm = () => (
        <form onSubmit={clickSubmit}>
            <div className="form-group">
                <label className="text-muted">Name</label>
                <input type="text" className="form-control" onChange={handleChange} value={name} required />
            </div>
            <button type="submit" className="btn btn-primary">
                Create
            </button>
        </form>
    );

    return (
        <React.Fragment>
            {newCategoryForm()}
            {categorieslistData()}
        </React.Fragment>
    );
};

export default Category;
